body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.title {
  margin: 0;
  font-size: 36px
}

.subtitle {
  margin: 0;
  color: grey;
}

.module-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: white;
  border: 2px solid black;
  border-radius: 5px;
  padding: 10px;
  margin-top: 10px;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.module-container:first-child {
  margin-top: 0px;
}

.title-container {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  border-bottom: 2px solid grey;
  padding-bottom: 5px;
}

.logo-container {
  height: 60px;
}

.logo {
  display: block;
  width: auto;
  height: 100%;
}

.titles {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-left: 10px;
}



