body {
  background-image: url("./images/background.jpg");
  width: 1080px;
  height: 1920px;
  background-repeat: no-repeat
}

.darken {
  position: absolute;
  top: 0;
  z-index: -1;
  left: 0;
  width: 1080px;
  height: 1920px;
  background: -webkit-linear-gradient(rgba(0,0,0,0.8)100px, rgba(0,0,0,0)500px, rgba(0,0,0,0.8));
  background: -o-linear-gradient(rgba(0,0,0,0.8)100px, rgba(0,0,0,0)500px, rgba(0,0,0,0.8));
  background: linear-gradient(rgba(0,0,0,0.8)100px, rgba(0,0,0,0)500px, rgba(0,0,0,0.8));
}

.app-container {
  height: 100%;
}

.content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 100%;
}

.filler {
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.top {
  color: white;
  margin: 30px;
}

.bottom {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 30px;
}

.modules {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.modules-left,
.modules-right {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-preferred-size: 0;
      flex-basis: 0;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.modules-left {
  margin-right: 10px;
}

.modules-right {
  margin-left: 10px;
}

.modules-left:first-child,
.modules-left:first-child {
  margin-top: 0px;
}



